import { createStore } from 'vuex';
import api from '../utils/api'

export default createStore({
  state: {
    adsWithPagination: [],
    tableOptions: {
      page: 1,
      sortBy:'',
      sortDesc: false
    },
    isLoadingAdsWithPagination: false,
    total: 0,
    isLoadingCurrentAdv: false,
    currentAdv:{},
    validationError: {}
  },
  mutations: {
    setIsLoadingAdsWithPagination: (state, payload) => state.isLoadingAdsWithPagination = payload,
    setIsLoadingCurrentAdv: (state, payload) => state.isLoadingCurrentAdv = payload,
    setTableOptionsToDefault: state => state.tableOptions = {
      page: 1,
      sortBy: '',
      sortDesc: false
    },
    setTableOptions: (state, payload) => state.tableOptions = {...payload},
    setPage: (state, payload) => state.tableOptions.page = payload,
    setAdsWithPagination: (state, payload) => {
      state.adsWithPagination = [...payload.data]
      state.total = payload.meta.total
    },
    setCurrentAdv: (state, payload) => state.currentAdv = {...payload},
    setSortBy(state, payload) {
      if (state.tableOptions.sortBy === payload) {
        if (state.tableOptions.sortDesc === false) {
          state.tableOptions.sortDesc = true
        } else {
          state.tableOptions.sortBy = ''
          state.tableOptions.sortDesc = false
        }
      } else {
        state.tableOptions.sortBy = payload
        state.tableOptions.sortDesc = false
      }
    },
    setValidationError: (state, payload)=> state.validationError = {...payload}
  },
  actions: {
    loadAds: async ({ commit, state }, object = {}) =>  {
      commit('setIsLoadingCurrentAdv', true)

      let params = {}
      if (object.filter) {
        commit('setTableOptionsToDefault')

        for (let key in object.filter) {
          if (!['null', ''].includes(object.filter[key])) {
            params[`filter[${key}][like]`] = object.filter[key]
          }
        }
      }

      params.page = state.tableOptions.page

      let sortBy = state.tableOptions.sortBy
      if (state.tableOptions.sortDesc) {
        sortBy = '-' + sortBy
      }
      params.sort = sortBy

      const res = await api.get('adv', { params })

      if (res) {
        commit('setAdsWithPagination', res)
      }
      commit('setIsLoadingCurrentAdv', false)
    },
    loadCurrentAdv: async({ commit }, obj) => {
      commit('setIsLoadingCurrentAdv', true)
      if (![null, undefined].includes(obj.id)) {
        let params = {}
        if (obj.additionalFields) {
          params.fields = 'images,description'
        }

        const { data } = await api.get(`adv/${obj.id}`, { params })
        if (data) {
          commit('setCurrentAdv', data)
        }
      }


      commit('setIsLoadingCurrentAdv', false)
    },
    createItem: async(context, obj) => {
      const { data } = await api.post('adv', obj)
      if (data.id) {
        return true
      }
      return false
    }
  }
})
